import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, NavigationEnd } from '@angular/router';

import { SharedService } from '../../shared/services/shared.service';
import { environment } from '../../../environments/environment';


@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.css']
})
export class ProductsComponent implements OnInit {

  _host 				  		= environment.config.BASE_URL;
  products 					    = [];
  page                          = 1;
  count							= 10;
  ratingIds						= [];
  categoryId					= '';
  brandId						= '';
  sortBy						= '';
  searchKeyword					= '';
  showMore						= true;
  categories                    = []
  categoryIds					= []
  brandArray					= [];
  productObj:any      = {};
  
  constructor(private _router: Router,
		private _route: ActivatedRoute,
		private _sharedService: SharedService) { }

  ngOnInit() {
  	this.search();
  	this.getAllCategories();
  	this.brandlist();
  }

    getAllCategories() {
      this._sharedService.get('categoryList').then(res => {
            if(res.success) {
                this.categories = res.data.category;
            } else {
            }
        },err => {  });
    }

    brandlist() {
      this._sharedService.get('brandlist').then(res => {
            if(res.success) {
                this.brandArray = res.data.data;
            } else {
            }
        },err => {  });
    }

  search(key = false) {
    	let options = {
    		page:this.page,
    		count:this.count,
    		brandId:this.brandId,
    		sortBy:this.sortBy,
    		search:this.searchKeyword
    	}
    	this._sharedService.loader('show');
    	this._sharedService.queryParamsProducts('prodwithfilter',options,this.categoryIds,this.ratingIds).then((res: any) => {
    		this._sharedService.loader('hide');
            if(res.success) {
            	let data = res.data.products;
            	if(key) {
	            	for (var i = 0; i < data.length; ++i) {
	                	this.products.push(data[i]);
	            	}
            	} else {
            		this.products = data;
            	}
    			if(data.length == 0)this.showMore = false;
            } else {
            }
        },(error) => { });	
    }

    clearAll() {
    	this.page    	   = 1;
		this.brandId 	   = ''
		this.sortBy  	   = ''
		this.searchKeyword = '';
		this.categoryIds   = [];
		this.ratingIds     = [];
    	this.search();
    }

    checkRating(id) {
    	let index = this.ratingIds.indexOf(id)
    	if(index >= 0) this.ratingIds.splice(index,1);
    	else this.ratingIds.push(id);
    	this.showMore = true;
    	this.page = 1;
    	this.search();
    }

    ratingR(rating) {
        if(rating) return parseInt(rating);
        else return 0;
    }

    setCategory(id) {
    	let index = this.categoryIds.indexOf(id)
    	if(index >= 0) this.categoryIds.splice(index,1);
    	else this.categoryIds.push(id);
    	this.showMore = true;
    	this.page = 1;
    	this.search();
   	}
	
	checkIfSelected(id) {
		if(this.categoryIds.indexOf(id) >=0) return true;
		else false;
	}

  selectedProduct(obj) {
      this.productObj = obj;
    }

    productFavorate() {
        this._sharedService.loader('show');
        let obj = {
            product_id:this.productObj['product_id'] ? this.productObj['product_id'] : this.productObj['id'],
            type:'product'
          }
          this._sharedService.post(obj,'favourite').then((res: any) => {
          this._sharedService.loader('hide');
                  this._sharedService.showAlert('Product Favorate Updated Successfuly.','alert-success')
          },(error) => { });
    }

}



