import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { SharedModule } from '../shared/shared.module';
import { NgxSpinnerModule } from 'ngx-spinner';

import { PagesRoutingModule } from './pages-routing.module';
import { ItemDetailComponent } from './item-detail/item-detail.component';
import { ViewItemsComponent } from './view-items/view-items.component';
import { SearchComponent } from './search/search.component';
import { ProductsComponent } from './products/products.component';
import { MapComponent } from './map/map.component';
import { NgxMapboxGLModule } from 'ngx-mapbox-gl';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    FormsModule,
    NgxSpinnerModule,
    PagesRoutingModule,
	NgxMapboxGLModule.withConfig({
      accessToken: 'pk.eyJ1IjoiamFpbjEiLCJhIjoiY2puZGVxbGE5MGM1bzNrbnllY2ZheWlueCJ9.vRaK9pxKg4CSiaU8dzoF4w' // Optionnal, can also be set per map (accessToken input of mgl-map)
      // geocoderAccessToken: 'TOKEN' // Optionnal, specify if different from the map access token, can also be set per mgl-geocoder (accessToken input of mgl-geocoder)
    })
  ],
  declarations: [ItemDetailComponent, ViewItemsComponent, SearchComponent, ProductsComponent, MapComponent]
})
export class PagesModule { }



